body {
  background-color: rgba(255, 158, 250, 0.89);
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;

}

#to-do-form input{
  background-color: rgb(74, 167, 196);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: white;
}

#to-do-form input::placeholder {
  color: rgba(255,255,255,0.5);
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(24, 202, 193);
}
